*,*::before,*::after{box-sizing:border-box}html,body{margin:0;padding:0}:root{--background: #000000;--foreground: #ffffff;--font-sans: var(--font-inter, sans-serif);--font-serif: var(--font-cormorant, serif)}body{background-color:var(--background);color:var(--foreground);font-family:var(--font-sans);font-size:16px;line-height:1.6}a{color:inherit;text-decoration:none}